.toggle {
  cursor: pointer;
  z-index: 100;
}

.x {
  position: absolute;
  top: 92px;
  left: 184px;
  height: 49px;
  width: 46px;
}

.ThumbPrint {
  position: absolute;
  right: 184px;
  bottom: 92px;
  height: 49px;
  width: 46px;
}

@media screen and (max-height: 320px), screen and (max-width: 320px) {
  .ThumbPrint {
    bottom: 18px;
    right: 18px;
    height: 39px;
    width: 36px;
  }

  .x {
    top: 18px;
    left: 18px;
    height: 39px;
    width: 36px;
  }

  .textbox, li {
    font-size: 12px;
  }

  .Modal {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 20px;
  }
}



