body {
  color: #4a4a4a;
  background: #1B2B2F;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

p, li {
  line-height: 1.75em;
  font-size: 18px;
  font-weight: 200;
  text-align: center;
}

h1 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 30px;
  font-weight: 200;
}

h3 {
  padding-bottom: 20px;
  font-size: 20px;
  text-align: left; }

.selector::after{
  content: '\A';
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

ol {
  padding: 0;
}

ol li {
  height: auto;
}



